@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700');

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 100vh;
  position: relative;
  background: #00284b;
}
.wrapper .container {
  background: #efefef;
  height: 600px;
  width: 960px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.container .title {
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
  background: #000 url('https://raw.githubusercontent.com/Shikigeru/React-Weather-App/gh-pages/images/title-background.png');
  color: #fff;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container .info {
  -ms-flex-preferred-size: 70%;
      flex-basis: 70%;
  padding: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #005cad;
  color: #fff;
}
.container .info p {
  font-size: 30px;
  text-transform: capitalize;
}
.container .info span {
  font-weight: bold;
}
.container .info form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.container .info form input {
  height: 40px;
  -ms-flex-preferred-size: 210px;
      flex-basis: 210px;
  margin-right: 10px;
  padding: 0 20px;
  border: none;
  text-transform: uppercase;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}
.container .info form input:last-child {
  margin-right: 0;
}
.container .info form button {
  display: inline-block;
  background: #00284b;
  color: #fff;
  border: none;
  height: 40px;
  padding: 0px 15px;
  text-transform: uppercase;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  outline: none;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}
.container .info form button:hover {
  background: #00335f;
}


@media screen and (max-width: 960px) {
  .container .info form input {
    width: 180px;
    -ms-flex-preferred-size: 180px;
        flex-basis: 180px;
  }
}

@media screen and (max-width: 856px) {
  .container .info form input {
    -ms-flex-preferred-size: 230px;
        flex-basis: 230px;
    margin: 0;
  }
  .container .info form button {
    margin: 10px auto 0 auto;
    width: 100%; 
  }
}

@media screen and (max-width: 800px) {
  .container .info form input {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    margin-top: 10px;
  }
  .container .info form input:first-child {
    margin-top: 0px;
  }
}

@media screen and (max-width: 670px) {
  .wrapper .container {
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100vh;
  }
  .container .title {
    -ms-flex-pack: center;
        justify-content: center;
  }
}

@media screen and (max-width: 480px) {
  .container .info p {
    font-size: 20px;
  }
  .container .title {
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
  }
  .container .info {
    -ms-flex-preferred-size: 80%;
        flex-basis: 80%;
  }
}


